import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import NavigationHeader from '../components/NavigationHeader';
import Footer from '../components/Footer';
import SentimentChart from '../components/SentimentChart';

function SessionDetail() {
  const { id } = useParams();
  const [session, setSession] = useState(null);
  const [statements, setStatements] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [sentimentData, setSentimentData] = useState(null);

  useEffect(() => {
    const fetchSessionData = async () => {
      try {
        setLoading(true);
        const [sessionRes, statementsRes, sentimentRes] = await Promise.all([
          axios.get(`/api/sessions/${id}/`),
          axios.get(`/api/sessions/${id}/statements/`),
          axios.get(`/api/sessions/${id}/sentiment_by_party/`)
        ]);

        setSession(sessionRes.data);

        // Handle different response formats for statements
        let statementsData = [];
        if (Array.isArray(statementsRes.data)) {
          statementsData = statementsRes.data;
        } else if (statementsRes.data && Array.isArray(statementsRes.data.data)) {
          statementsData = statementsRes.data.data;
        } else if (statementsRes.data && Array.isArray(statementsRes.data.results)) {
          statementsData = statementsRes.data.results;
        }

        setStatements(statementsData);
        setSentimentData(sentimentRes.data);
      } catch (err) {
        setError('    .');
        console.error('Error fetching session data:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchSessionData();
  }, [id]);

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center text-red-600 p-4">
        {error}
      </div>
    );
  }

  if (!session) {
    return (
      <div className="text-center text-gray-600 p-4">
            .
      </div>
    );
  }

  return (
    <div className="flex overflow-hidden flex-col bg-white min-h-screen">
      <NavigationHeader />
      <main className="flex flex-col w-full">
        <div className="container mx-auto px-4 py-8">
          {/* Session Header */}
          <div className="bg-white rounded-lg shadow p-6 mb-8">
            <h1 className="text-3xl font-bold mb-4">
              {session.title || `${session.era_co} ${session.sess} ${session.dgr}`}
            </h1>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <p className="text-gray-600">
                  <span className="font-semibold">:</span>{' '}
                  {session.conf_dt ? new Date(session.conf_dt).toLocaleDateString('ko-KR') : '  '}
                </p>
                <p className="text-gray-600">
                  <span className="font-semibold">:</span>{' '}
                  {session.bg_ptm && session.ed_ptm ? `${session.bg_ptm} - ${session.ed_ptm}` : '  '}
                </p>
              </div>
              <div>
                <p className="text-gray-600">
                  <span className="font-semibold">:</span>{' '}
                  {session.conf_knd || ' '}
                </p>
                <p className="text-gray-600">
                  <span className="font-semibold">:</span>{' '}
                  {session.conf_plc || ' '}
                </p>
              </div>
            </div>
          </div>

          {/* Sentiment Analysis */}
          <div className="bg-white rounded-lg shadow p-6 mb-8">
            <h2 className="text-2xl font-bold mb-4">  </h2>
            <SentimentChart data={sentimentData} />
          </div>

          {/* Statements List */}
          <div className="bg-white rounded-lg shadow p-6">
            <h2 className="text-2xl font-bold mb-4"> </h2>
            <div className="space-y-6">
              {Array.isArray(statements) && statements.map(statement => (
                <div key={statement.id} className="border-b pb-6 last:border-b-0">
                  <div className="flex items-center justify-between mb-2">
                    <div>
                      <h3 className="text-lg font-semibold">
                        {statement.speaker_name || statement.speaker?.naas_nm || '  '}
                      </h3>
                      <p className="text-sm text-gray-600">
                        {statement.party_name || statement.speaker?.plpt_nm || '  '}
                      </p>
                    </div>
                    {statement.sentiment_score !== null && statement.sentiment_score !== undefined && (
                      <div className="text-sm">
                        <span className={`px-2 py-1 rounded ${
                          statement.sentiment_score > 0.3 ? 'bg-green-100 text-green-800' :
                          statement.sentiment_score < -0.3 ? 'bg-red-100 text-red-800' :
                          'bg-gray-100 text-gray-800'
                        }`}>
                           : {statement.sentiment_score.toFixed(2)}
                        </span>
                      </div>
                    )}
                  </div>
                  <p className="text-gray-700 whitespace-pre-wrap">
                    {statement.text || statement.content || '  '}
                  </p>
                </div>
              ))}
              {(!Array.isArray(statements) || statements.length === 0) && (
                <div className="text-center text-gray-500 py-8">
                    .
                </div>
              )}
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </div>
  );
}

export default SessionDetail;